            margin-bottom: 100px;

            &:hover {
                background-color: crimson; 
                color: #fff;
                transition: 0.2s;
            }
        }

        .fiveLower {
            overflow: hidden;

            ul {
                &::after{content: ""; display: block; clear: both;}
                width: 1700px;
                li {
                    float: left;
                    width: 400px;
                    height: 600px;
                    margin-right: 25px;
