                    &:nth-of-type(3){.fivePic {background-image: url(../../public/img/covid3.jpg);}}
                    &:nth-of-type(4){.fivePic {background-image: url(../../public/img/covid4.jpg);}}

                    .fivePic {
                        width: 400px;
                        height: 220px;
                        border-radius: 10px;
