                display: inline-block;
                margin-right: 15px;
                font-size: 25px;
                width: 40px;
                height: 40px;
                border: 2px solid #000;
                color: #000;
                border-radius: 50%;
                // line-height: 40px;
                transition: 0.2s;

                transform: translateY(3px);

                i {
                    margin-left: 7px;
                }

                &:hover {
                    color: #fff;
                    border-bottom: none;
                    border: 2px solid #555;
                    background-color: #555;
                    transition: 0.2s;
                }
            }
        }
    }

